.service-card{
    width: 100%;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset, rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
    background-color: rgba(255, 255, 255, 0);
    padding: 35px;
}

.service-card-image{
    max-height: 100%;
    max-width: 100%;
    width: 450px;
    height: 400px;
    display: block;
    box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
    border-radius: 50%;
}

.service-card-image:hover{
    transform: scale(1.04);
    transition: all ease-in-out 0.4s;
}

.service-card-text{
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-right: 20px;
    width: 70%
}

.right{
    margin-right: auto;
}

p{
    font-family: 'Libre Baskerville', serif;
}

.description{
    margin-top: auto;
    margin-bottom: auto;
    font-family: 'Libre Baskerville', serif;
    padding: 10px;
    border-radius: 13px;
    width: 80%
}

.title {
    margin-top: 0;
    text-align: center;
    font-weight: 900;
    font-family: 'Lobster', cursive !important;
}


h1{
    font-family: 'Lobster', cursive !important;
}

h2{
    font-family: 'Lobster', cursive !important;
}

h3{
    font-family: 'Lobster', cursive !important;
}

h4{
    font-family: 'Lobster', cursive !important;
}

@media(max-width: 991.98px){
.right .service-card-image{
    margin-right: 250px;
    width: 380px;
    height: 400px;
    margin-left: 0;
    box-shadow:rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

    .service-card-image{
        width: 380px;
        height: 400px;
        margin-left: 0;
        box-shadow:rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    }
}

@media (max-width: 767.98px){
.service-card{
    padding-left: 5px;
}

    .service-card-image{
        width: 200px;
        height: 200px;
        margin-left: 6px;
        margin-right: 5px;
    }

    .right .service-card-image{
        margin-right: 150px;
        width: 200px;
        height: 200px;
        margin-left: 15px;
    }
}

@media (max-width: 575.98px){
    .service-card{
        padding-left: 5px;
    }
    
        .service-card-image{
            width: 200px;
            height: 200px;
            margin-left: 6px;
            margin-right: 5px;
        }
    
        .right .service-card-image{
            margin-right: 150px;
            width: 200px;
            height: 200px;
            margin-left: 15px;
        }

         .service-card-text{
            margin-left: 20px;
        }

        .description{
            font-size: 0.85em;
        }
    }